import React from 'react';
import * as S from "./card.styles";

const Card = (props) => {
    return (
        <S.CardContainer
            width={props.width}
            maxWidth={props.maxWidth}
            height={props.height}
            textAlign={props.textAlign}
            margin={props.margin}
            style={props.styles}
            {...props}
        >
            {props.children}
        </S.CardContainer>
    )
}

export default Card;
