import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import * as S from "./evaluationOfOverallCondition.styles";

import Topbar from "components/topbar/Topbar";
import EvaluationCard from "components/evaluationCard/evaluationCard";
import { EVALUATION_ITEMS } from "./EvaluationData";

// Redux
import * as AuthActions from "redux/auth/auth.actions";

const EvaluationOfOverallCondition = () => {
  const dispatch = useDispatch();
  const history = useHistory();
  const userAnamneses = useSelector((state) => state.auth.userAnamneses);

  const [Evaluations, setEvaluations] = useState(EVALUATION_ITEMS);
  const [isLoading, setIsLoading] = useState(false);
  // console.log("userAnamneses", userAnamneses);
  useEffect(() => {
    setIsLoading(true);
    // dispatch(AuthActions.getUserAnamneseSuccess(null))
    dispatch(
      AuthActions.getUserAnamneseRequest({
        formData: { userID: localStorage.getItem("AnamneseUserId"), weekID: 1 },
        callback: (response) => {
          console.log("userAnamneses", userAnamneses);
          setIsLoading(false);
          displayAnamnese(response?.data[0]);
        },
      })
    );
  }, []);

  const updateFormValues = (list, obj, property) => {
    return list.map((i) => {
      if (i.state === obj[property]) {
        i.isActive = true;
      }

      return i;
    });
  };

  const displayAnamnese = (userAnamneses) => {
    const evaluations = EVALUATION_ITEMS.map((obj) => {
      const item = { ...obj };

      if (item.title === "Stresslevel") {
        item.images = updateFormValues(
          item.images,
          userAnamneses,
          "stress_level"
        );
      }
      if (item.title === "Krperform") {
        item.images = updateFormValues(item.images, userAnamneses, "body_fat");
      }
      if (item.title === "Sportliche Aktivitt") {
        item.numbers = updateFormValues(
          item.numbers,
          userAnamneses,
          "sport_activity"
        );
      }
      if (item.title === "Tgliche Aktivitt") {
        item.numbers = updateFormValues(
          item.numbers,
          userAnamneses,
          "daily_activity"
        );
      }
      if (item.title === "Rauchen") {
        item.numbers = updateFormValues(item.numbers, userAnamneses, "smoking");
      }
      if (item.title === "Alkohol") {
        item.numbers = updateFormValues(item.numbers, userAnamneses, "alcohol");
      }

      if (item.title === "Obst & Gemse") {
        item.numbers = updateFormValues(
          item.numbers,
          userAnamneses,
          "fruits_vegitables"
        );
      }
      if (item.title === "Mahlzeitenfrequenz") {
        item.numbers = updateFormValues(
          item.numbers,
          userAnamneses,
          "meal_frequency"
        );
      }
      if (item.title === "Trinken") {
        item.numbers = updateFormValues(
          item.numbers,
          userAnamneses,
          "drinking"
        );
      }
      if (item.title === "Heihunger") {
        item.numbers = updateFormValues(
          item.numbers,
          userAnamneses,
          "cravings"
        );
      }
      if (item.title === "Stimmungsschwankungen") {
        item.numbers = updateFormValues(
          item.numbers,
          userAnamneses,
          "mood_swings"
        );
      }
      if (item.title === "Schwchegefhl") {
        item.numbers = updateFormValues(
          item.numbers,
          userAnamneses,
          "weekness_feeling"
        );
      }
      return item;
    });

    // console.log("evaluations", evaluations)
    setEvaluations(evaluations);
  };

  return (
    <>
      <Topbar
        prev="/healthscore/priority"
        next="/healthscore/tips"
        progress={"18%"}
        close
        onClose={() => history.push("/login")}
      />
      <S.EvaluationContainer>
        <S.EvaluationInnerContainer>
          <S.EvaluationHeading>Auswertung</S.EvaluationHeading>
          <S.EvaluationSubHeading>
            Bewertung deiner aktuellen Gesamtverfassung
          </S.EvaluationSubHeading>

          {isLoading && (
            <>
              <br />
              <br />
              <br />
              <br />
              <div className="text-center">
                <div className="loader mx-auto"></div>
              </div>
              <br />
              <br />
              <br />
              <br />
            </>
          )}
          {!isLoading && (
            <S.GridView>
              {Evaluations.map((item) => (
                <EvaluationCard item={item} />
              ))}
            </S.GridView>
          )}
        </S.EvaluationInnerContainer>
      </S.EvaluationContainer>
    </>
  );
};

export default EvaluationOfOverallCondition;
