import React from "react";
import { Formik } from "formik";
import * as Yup from "yup";
import * as S from "./changePassword.styles";
import { useDispatch, useSelector } from "react-redux";
import Slider from "react-slick";
// Components
import Topbar from "components/topbar/Topbar";
import Card from "components/card/Card";
import Input from "components/formComponents/input/Input";
import Button from "components/formComponents/button/Button";
import Loader from "components/loader/Loader";
import ErrorMessage from "components/formComponents/errorMessage/ErrorMessage";
import FoodiaryLogo from "assets/images/Foodiary-Logo.PNG";
import TranslationMessage from "configs/translations/TranslationMessage";
import FoodiaryBackground from "assets/images/Background-foodiary-password-user.jpg";
import DashboardOne from "assets/images/Foodiary-set-password-slide1.svg";
import DashboardTwo from "assets/images/Foodiary-set-password-slide2.svg";
import DashboardThree from "assets/images/Foodiary-set-password-slide3.svg";
import ExclamationCircle from "assets/images/exclamation-circle.svg";
import Hand from "assets/images/YoHand.svg";
// Redux
import * as AuthActions from "redux/auth/auth.actions";

const ChangePassword = () => {
  const dispatch = useDispatch();
  const loading = useSelector((state) => state.auth.loading);

  const changePasswordRedirection = () =>
    window.location.assign("https://my.foodiary.app/");

  const initialValues = {
    email: "",
    password: "",
    confirmPassword: "",
  };

  const validationSchema = Yup.object().shape({
    email: Yup.string()
      .email("E-Mail muss bei uns registriert sein")
      .required("Erforderlich"),
    password: Yup.string()
      .required("Erforderlich")
      .min(8, "Passwort muss mindestens 8 Zeichen lang sein"),
    confirmPassword: Yup.string()
      .required("Erforderlich")
      .oneOf([Yup.ref("password"), null], "Passwort muss bereinstimmen"),
  });

  const onFormSubmit = (values, { resetForm }) => {
    dispatch(
      AuthActions.changePasswordRequest({
        formValues: {
          email: values.email,
          password: values.password,
          confirm_password: values.confirmPassword,
        },
        func: changePasswordRedirection,
      })
    );
    resetForm();
  };

  let settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
  };

  return (
    <>
      <S.LoginContainer>
        <Card
          width="100%"
          maxWidth="530px"
          height="auto"
          margin="auto"
          textAlign="center"
        >
          <S.ChangePasswordInnerContainer className="relative">
            <img width={153} src={FoodiaryLogo} alt="" />
            <S.ChangePasswordHeading>
              <TranslationMessage id="changePassword.heading" />{" "}
            </S.ChangePasswordHeading>
            <S.ChangePasswordSubHeading>
              <TranslationMessage id="changePassword.subHeading" />
            </S.ChangePasswordSubHeading>
            <Formik
              initialValues={initialValues}
              validationSchema={validationSchema}
              onSubmit={onFormSubmit}
            >
              {({
                handleSubmit,
                handleChange,
                handleBlur,
                values,
                touched,
                errors,
                isValid,
                dirty,
              }) => (
                <form
                  className="relative z-1"
                  noValidate
                  autoComplete="off"
                  onSubmit={handleSubmit}
                >
                  <div className="form-group">
                    <label>E-Mail Adresse*</label>
                    <Input
                      className="input-v2"
                      type="text"
                      placeholder="Ihre E-Mail Adresse"
                      name="email"
                      onChange={handleChange}
                      value={values.email}
                    />
                    <ErrorMessage error={errors.email} />
                  </div>
                  <div className="form-group">
                    <label>Neues Passwort eingeben*</label>
                    <Input
                      className="input-v2"
                      type="password"
                      placeholder="neues Passwort"
                      name="password"
                      onChange={handleChange}
                      value={values.password}
                    />
                    <ErrorMessage error={errors.password} />
                  </div>

                  <div className="form-group">
                    <label>Neues Passwort besttigen*</label>
                    <Input
                      className="input-v2"
                      type="password"
                      placeholder="neues Passwort"
                      name="confirmPassword"
                      onChange={handleChange}
                      value={values.confirmPassword}
                    />
                    <ErrorMessage error={errors.confirmPassword} />
                    <p className="input-note">
                      <img src={ExclamationCircle} alt="" /> Gebe die E-Mail
                      ein, mit der du dich registriert hast.
                    </p>
                  </div>

                  <Button
                    color="rgba(248, 164, 146, 1)"
                    gradient="linear-gradient(60deg, rgba(46, 207, 150, 1), rgba(28, 157, 135, 1)) !important"
                    style={{ fontSize: "16px" }}
                    margin="40px 0 20px"
                    absolute={{ width: "100%", height: "45px" }}
                    buttonStyle={{ width: "100%" }}
                    type="submit"
                  >
                    <TranslationMessage id="button.changePassword" />
                  </Button>

                  <Loader loading={loading} top="-135px" />
                </form>
              )}
            </Formik>
            <img className="hand" src={Hand} alt="" />
          </S.ChangePasswordInnerContainer>
        </Card>
        {/* <S.SliderContainer imgUrl={FoodiaryBackground}>
          <Slider {...settings}>
            <div className="slide-item">
              <img src={DashboardOne} alt="" />
              <h4>Lernacademy</h4>
              <p>
                In unserer Lernacademy findest du unseren 8-Wochen
                Ernhrungskurs und weiter hilfreiche Artikel
              </p>
            </div>
            <div className="slide-item">
              <img src={DashboardTwo} alt="" />
              <h4>Dashboard</h4>
              <p>
                Das Dashboard zeigt dir eine strucktuierte bersicht deines
                Lernerfolges an. Zudem ist es mit der Ernhrungsplaner-App
                verknpft.
              </p>
            </div>
            <div className="slide-item">
              <img src={DashboardThree} alt="" />
              <h4>E-Books</h4>
              <p>
                In unseren E-Books gehen wir im Detail auf die unterschiedlichen
                Bereiche der gesunden Ernhrung ein und du eignest dir ein
                tiefes Wissen an.
              </p>
            </div>
          </Slider>
        </S.SliderContainer> */}
      </S.LoginContainer>
    </>
  );
};

export default ChangePassword;
