export const ClientDetailsTab = styled.div`
  color: ${(props) => (props.active ? "#1C9D87" : "#44554C")};
  cursor: pointer;
  font-size: 15px;
  line-height: 18px;
  font-weight: 400;
  margin-bottom: 8px;
  position: relative;
  padding: 8px 15px;
  border: 1px solid transparent;

  &.invoice-details {
    display: none;
  }

  &:hover {
    color: #1c9d87;
    box-shadow: 0px 1px 2px #1018280d;
    border-color: #d0d5dd;
    border-radius: 10px;
    background: white;
  }

  &:after {
    content: "";
    position: absolute;
    right: 6px;
    top: 10px;
    background-image: ${(props) =>
      props.active ? `url("${RightArrow}")` : "none"};
    width: 16px;
    height: 16px;
  }

  ${(props) =>
    props.active
      ? {
          boxShadow: "0px 1px 2px #1018280d",
          border: "1px solid #d0d5dd",
          borderRadius: "10px",
          background: "white",
        }
      : null}
`;

